.NavbarItems {
    background: linear-gradient(90deg, rgb(64, 26, 48) 0%, rgb(0, 0, 0)100%);
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2em;
}

.navbar-logo {
    color: white;
    justify-self: start;
    margin-left: 20px;
    cursor: pointer;
    font-size: 2VW;
}

.fa-react {
    margin-left: 0.5rem;
    font-size: 1.6rem;
}

.nav-menu {
    display: grid;
    grid-template-columns: repeat(5,auto);
    grid-gap: 10px;
    list-style: none;
    text-align: center;
    width: 70vw;
    justify-content: end;
    margin-right: 2rem;
}

.nav-links {
    color: white;
    text-decoration: none;
    padding: .05rem 1rem;
}

.nav-links:hover {
    background-color: #66dd77;
    border-radius: 4px;
    transition: all 0.2s ease-out;
}

.fa-bars {
    color: white;
}

.nav-links-mobile {
    display: none;
}

.menu-icon {
    display: none;
}

@media screen and (max-width: 1845px) {
    .fa-react {
        display: none;
    }
}

/* @media screen and (max-width: 1393px) {
    .navbar-logo {
        display: none;
    }
} */

@media screen and (max-width: 768px) {
    .navbarItems {
        position:relative;

    }

    .container-fluid {
        display: flex;
    }

    .nav-menu {
        display: flex;
        flex-direction:column;
        width: 100%;
        height: 500px;
        position: absolute;
        top: 80px;
        left: -100%;
        opacity: 1;
        transition: all 0.5s ease;
    }

    .nav-menu.active {
        background: rgb(156, 3, 41);
        left: 5;
        opacity: 1;
        transition: all 0.5s ease;
        z-index: 1;
    }

    .nav-links {
        text-align: center;
        padding: 2rem;
        width: 100%;
        display: table;
    }

    .nav-links:hover {
        background-color: #f577fa;
        border-radius: 0;

    }

    .navbar-logo {
        position: absolute;
        text-align: left;
       
        transform: translate(25%, 50%);
    }

    .menu-icon {
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        transform: translate(-100%, 60%);
        font-size: 1.8 rem;
        cursor: pointer;
    }

    .fa-times{

        color: white;
        font-size: 2rem;
    }

    .nav-links-mobile {
        display: block;
        text-align: center;
        padding: 1.5rem;
        margin: 2rem auto;
        border-radius: 4px;
        width: 80%;
        background: #4ad9e4;
        text-decoration: none;
        color: white;
        font-size: 1.5rem;
    

    }
    .nav-links-mobile {
        background: white;
        color: #4ad9e4;
        transition: 250ms;
    }

    .Button {
        display: none;
    }
}